    .btn {

// - - - DEFAULT

        position: relative;
        display: inline-block;
        background-color: $black;
        color: $white;
        font-family: $font-bread;
        font-weight: bold;
        font-size: 24px;
        line-height: 48px;
        line-height: 48px;
        border-radius: 50px;
        padding: 0 30px;
        text-align: center;
        cursor: pointer;
        text-decoration: none;
        border: none;


// - - - COLORS

        &.-icon {
          position: relative;
          overflow: hidden;
          padding-right: 64px;
          svg {
            position: absolute;
            right: 20px;
            fill: $white;
            width: 30px;
            height: 48px;
            display: inline-block;
          }
        }

         &.-icon-only {
          // position: relative;
          // overflow: hidden;
          // padding-right: 64px;
          padding-right: 64px;
          padding-left: 64px;
          height: 48px;
          svg {
            // position: absolute;
            // right: 20px;
            fill: $white;
            width: 30px;
            height: 48px;
            display: inline-block;
          }
        }


// - - - HELPERS

        &.-shadow {
            @include box-shadow(-2px, 2px, 18px, rgba(0, 0, 0, 0.35), $inset: false);
        }

        &.-block {
            display: block;
        }

        &.-text-left {
            text-align: left;
        }

        &.-square {
          border-radius: 0;
        }



// - - - INTERACTION

        &:hover {
             background:lighten($black,20%);
             color: $white;
        }

        &:active {
             background: lighten($black,20%);
        }

        &:visited {
            color: $white;
        }

        &:focus {
            outline: none;
        }


// - - - COLORS

        &.-green {
          background-color: $green;
          &:hover {
               background:lighten($green,12%);
               color: $white;
          }
          &:active {
               background: lighten($green,12%);
          }
          &:visited {
              background-color: $green;
          }
        }

        &.-orange {
          background-color: $orange;
          &:hover {
               background:lighten($orange,12%);
               color: $white;
          }
          &:active {
               background: lighten($orange,12%);
          }
          &:visited {
              background-color: $orange;
          }
        }

        &.-yellow {
          background-color: $yellow;
          &:hover {
               background:lighten($yellow,12%);
               color: $white;
          }
          &:active {
               background: lighten($yellow,12%);
          }
          &:visited {
              background-color: $yellow;
          }
        }


// LATER - Teknisk skuld "touch / no-touch"

        /*

            .no-touch & {

            }

            .touch & {

            }

        */


    }


  .btn-choice  {
    display: block;
    padding: 12px 0;
    background-color: $grey4;
    color: $grey2;
    text-align: center;
    font-weight: 500;
    border: 1px solid $grey4;
    margin-bottom: 24px;

    &:hover {
      color: $black;
      text-decoration: underline;
    }

    &.-active {
      background-color: $white;
      border-color: $black;
      color: $black;
    }
  }


