//
// S-SHARE
//

.s-share {

    background: $black;
    padding: 5px 0;

    .logo {
        display: none;
    }

    .header {
        display: none;
        color: $grey3;
        margin:20px 0 0 0;
    }

    ul {
        margin: 0;
        padding: 0;
        display: block;
        text-align: center;

        a {
            display: inline-block;
            margin: 8px 0 0 0;

            svg {
                fill: $white;
                width: 50px;
                height: 50px;

            }

            &:hover {
                svg {
                    fill: $yellow;
                }
            }

        }

    }
    li {
        display: inline-block;
        margin:5px 10px ;
    }

    @media screen and (min-width: $bp-600) {
        .header {
            display: inline-block;
            float: left;
            margin: 22px 15px 0 0;
        }

        ul {
            float: left;
            display: block;
            text-align: left;

            li {
                float: left;

            }

        }
    }

    @media screen and (min-width: $bp-768) {
        .logo {
            display: block;
            margin-top: 20px;
            svg {
                fill: $white;
                width: 100px;
                height: 30px;
            }
        }
    }

    @media screen and (min-width: $bp-1024) {
        .header {
            margin: 22px 30px 0 0;
        }
    }


}

